.taskList {
    margin: 0 auto;
    width: 290px;
    /*border: 1px solid #000;*/
    /*border-radius: 15px;*/
    padding: 0  15px 15px;
    /*background-color: #a4b97d;*/
}

.taskItem {
    width: 100%;
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    align-items: center;
}
.taskItem:last-child {
    margin-bottom: 0;
}

.isDone {
    opacity: 0.8;
}

.errorMessage {
    display: block;
    margin: 15px auto 0;
    text-align: center;
    font-family: 'Arial';
    font-size: 16px;
    font-weight: bold;
    color: #ca4b3f;
    letter-spacing: 1px;
}

.activeFilter {
    background-color: aquamarine;
}

.controls {
    margin-top: 12px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 10px;
}

button {
}

input[type='checkbox'] {
    display: none;
}

input[type='checkbox']+label{
    padding: 5px 10px;
    max-width: 180px;
    font-weight: bold;
    color: #036171;
    border: none;
    background-color: rgba(164, 185, 125, 0.58);
    border-radius: 5px;
    cursor: pointer;
}

input[type='checkbox']:checked+label{
    color: #44444499;
    background-color: rgba(141, 166, 90, 0.25);
    text-decoration: line-through;
}

.counterWrapper {
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 8px;
}

.counterWrapper span {
    color: #3f4471;
    font-weight: bold;
}

.counter {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border: 1px solid #3f4471;
    border-radius: 50%;
    color: #3f4471;
}

